import React, { useState } from 'react';
import instance from '../axios'


function DepartmentForm(props) {
    const csrftoken = window.CSRF_TOKEN;
    const [name, setName] = useState()
    const value = props.value


    {/* ------------ SAVE DEPARTMENT -------------*/ }

    function saveDepartment(e) {
        e.preventDefault();
        const postData = {
            name
        };
        instance.post('/department/', postData).then((response) => { props.after(response.data) })
        var n = document.getElementById('dep_name');
        n.value = '';
    };

    {/* ------------ DEPARTMENT COMPONENT -------------*/ }

    return (
        <div className="department-form">
            <form method="post" className="form-input" onSubmit={saveDepartment}>
                <input name="csrfmiddlewaretoken" value={csrftoken} hidden />
                <input className="Inputext" type="text" name="dep_name" id="dep_name" placeHolder="Nombre de departamento" value={value} onChange={(e) => setName(e.target.value)} required />
                <button type="submit" className="btn btn-outline-success" onClick={() => (window.confirm(`¿Confirma guardar "${name}"?`))} ><i class="bi bi-check-lg"></i></button>
            </form>
        </div>
    )
}

export default DepartmentForm;